import { createContext, useState } from "react";

const MarkerModalContext = createContext();

const MarkerModalProvider = ({ children }) => {
  const [showModal, setShowModal] = useState(false);
  const [activeMarker, setActiveMarker] = useState(null);

  const store = {
    showModal,
    setShowModal,
    activeMarker,
    setActiveMarker,
  };

  return (
    <MarkerModalContext.Provider value={store}>
      {children}
    </MarkerModalContext.Provider>
  );
};

export { MarkerModalContext, MarkerModalProvider };
